
@import "../variables.scss";


ol,ul{list-style:none;margin:0;padding:0;}
blockquote,q{quotes:none;}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none;}
table{border-collapse:collapse;border-spacing:0;}
/* start editing from here */
a{text-decoration:none;}
.txt-rt{text-align:right;}/* text align right */
.txt-lt{text-align:left;}/* text align left */
.txt-center{text-align:center;}/* text align center */
.float-rt{float:right;}/* float right */
.float-lt{float:left;}/* float left */
.clear{clear:both;}/* clear float */
.pos-relative{position:relative;}/* Position Relative */
.pos-absolute{position:absolute;}/* Position Absolute */
.vertical-base{ vertical-align:baseline;}/* vertical align baseline */
.vertical-top{  vertical-align:top;}/* vertical align top */
.underline{ padding-bottom:5px; border-bottom: 1px solid #eee; margin:0 0 20px 0;}/* Add 5px bottom padding and a underline */
nav.vertical ul li{ display:block;}/* vertical menu */
nav.horizontal ul li{ display: inline-block;}/* horizontal menu */
img{max-width:100%;}
/*end reset*/
/*--header start here--*/
body{
  font-family: 'Montserrat', sans-serif;
  font-size: 100%;
  background: url('../../fotos/fondo_login.jpg')no-repeat;
  background-size: cover;
}

a {

  cursor: pointer;
}
.login-form{
  
  padding:100px 0px 50px 0px;

}
.login-form h1{
  font-size:2em;
  color:#fff;
  font-weight:800;
  text-align:center;
  margin-bottom:2em;

}
.top-login {
    width: 130px;
    height: 130px;
    display: block;
    -webkit-transform: rotate(45deg) translate3d(0, 0, 0);
    -moz-transform: rotate(45deg) translate3d(0, 0, 0);
    -ms-transform: rotate(45deg) translate3d(0, 0, 0);
    -o-transform: rotate(45deg) translate3d(0, 0, 0);
    transform: rotate(45deg) translate3d(0, 0, 0);
    margin: 0 auto 4em;
    background: #fff;
    position: relative;
}
.top-login span{
    border: 2px solid $color-gral;
    width: 105px;
    height: 105px;
    display: block;
    margin: 0px auto;
    position: absolute;
    top: 11px;
    left: 11px;
}
.top-login span img{
    -webkit-transform: rotate(-45deg) translate3d(0, 0, 0);
    -moz-transform: rotate(-45deg) translate3d(0, 0, 0);
    -ms-transform: rotate(-45deg) translate3d(0, 0, 0);
    -o-transform: rotate(-45deg) translate3d(0, 0, 0);
    transform: rotate(-45deg) translate3d(0, 0, 0);
    margin: 20px 0 0 20px;
}
/*----*/

.login-top{
  width: 460px;
  display: block;
  margin: 0 auto;
}
.login-ic {
    background: rgba(255, 255, 255, 0.32); 
  margin-bottom:1.5em;
    padding: 8px; 
}
.login-ic i {
        background: url('../../fotos/m.png')no-repeat 6px 6px;
    width: 38px;
    height: 38px;
    float: left;
  /*-- agileits --*/
    display: inline-block;
}
.login-ic i.icon {
    background: url('../../fotos/l.png')no-repeat 6px 6px;  
}
.login-ic input[type="email"],.login-ic input[type="text"],.login-ic  input[type="password"] {
      float: left;
    background: none;
    outline: none;
    font-size: 15px;
    font-weight: 400;
    color: #fff;
    padding: 10px 16px;
    border: none;
    border-left: 1px solid #fff;
    width: 82%;
    display: inline-block;
    margin-left: 7px;
}
.log-bwn {
  text-align: center;
}
.log-bwn input[type="submit"] {
    font-size: 15px;
    font-weight: 700;
    color: #fff;
    padding: 13px 0;
    background: $color-gral;
    display: inline-block;
    width: 100%;
  outline:none;
  border:2px solid $color-gral;
  cursor:pointer;
  text-transform:uppercase;
}
.log-bwn input[type="submit"]:hover{
  transition: all 0.3s ease;
     -webkit-transition: all 0.3s ease;
     -moz-transition: all 0.3s ease;
     -o-transition: all 0.3s ease;
   border:2px solid #fff;
}
/*-- profile_2 --*/
p.copy{
  color:#fff;
  font-size:1em;
  text-align:center;
  margin-top:6em;
}
/*-- w3layouts --*/
p.copy a{
  color:#fff;
  text-decoration:underline;
}
p.copy a:hover{
  text-decoration:none;
}

/*--meadia quiries start here--*/
@media (max-width:1024px){
  p.copy {
      margin-top: 3.5em;
  }
  .login-form {
      padding: 60px 0px 40px 0px;
  }
}

@media (max-width:768px){
  body {
      min-height: 929px;
  }
}
@media (max-width:768px){
  body {
      min-height: auto;
  }
}
@media (max-width:600px){
  .login-top {
      width: 425px;
  }
  .login-ic input[type="email"],.login-ic input[type="text"], .login-ic input[type="password"] {
      width: 79%;
  }
}
@media (max-width:480px){
  p.copy {
      font-size: 0.9em;
    padding:0 0.5em;
    line-height: 1.8em;
  }
}
@media (max-width:450px){
  .login-top {
      width: 360px;
  }
  .login-ic input[type="email"],.login-ic input[type="text"], .login-ic input[type="password"] {
      width: 76%;
    /*-- agileits --*/
  }
  .top-login {
      width: 90px;
      height:90px;
      margin: 0 auto 3em;
  }
  .top-login span img {
      margin: 5px 0 0 5px;
  }
  .top-login span {
      width: 75px;
      height: 75px;
      top: 5px;
      left: 5px;
  }
  .login-form h1 {
      margin-bottom: 1em;
      font-size: 1.7em;
  }
  p.copy {

      margin-top: 3em;
  }
  .login-form {
      padding: 80px 0px 45px 0px;
  }
  .login-ic {
      margin-bottom: 1em;
      padding: 3px;
  }
  .log-bwn input[type="submit"] {
      padding: 10px 0;
  }
  p.copy {
      font-size: 0.8em;
  }
  body {
      min-height: 672px;
  }
}
@media (max-width:384px){
  .login-top {
      width: 340px;
  }
  .login-ic input[type="email"],.login-ic input[type="text"], .login-ic input[type="password"] {
      width: 75%;
  }
  body {
      min-height: 600px;
  }
}
@media(max-width:320px){
  .login-top {
      width: 280px;
  }
  .login-ic input[type="email"],.login-ic input[type="text"], .login-ic input[type="password"] {
      width: 70%;
  }
  .login-form {
      padding: 50px 0px 45px 0px;
  }
  body {
      min-height: 540px;
  }
}

.social-login {
  margin-top: 35px;
}

.social-login h3 {
  color: #fff;
}

.social-login-buttons {
  margin-top: 25px;
}

.btn-link-1,.btn-link-3{
  display: inline-block;
  height: 50px;
  margin: 5px;
  padding: 16px 20px 0 20px;
  background: #de615e;
  font-size: 16px;
    font-weight: 300;
    line-height: 16px;
    color: #fff;
    -moz-border-radius: 4px; -webkit-border-radius: 4px; border-radius: 4px;
}
.btn-link-1:hover, .btn-link-1:focus, .btn-link-1:active { outline: 0; opacity: 0.6; color: #fff; }

.btn-link-1 { background: #4862a3; }
.btn-link-2 { background: #55acee; }
.btn-link-3 { background: #dd4b39; }

.btn-link-1 i {
  padding-right: 5px;
  vertical-align: middle;
  font-size: 20px;
  line-height: 20px;
}

.btn-link-2 {
  display: inline-block;
  height: 50px;
  margin: 5px;
  padding: 15px 20px 0 20px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #fff;
  font-size: 16px;
    font-weight: 300;
    line-height: 16px;
    color: #fff;
    -moz-border-radius: 4px; -webkit-border-radius: 4px; border-radius: 4px;
}
.btn-link-2:hover, .btn-link-2:focus, 
.btn-link-2:active, .btn-link-2:active:focus { outline: 0; opacity: 0.6; background: rgba(0, 0, 0, 0.3); color: #fff; }
/*--media quiries end here--*/